.error404 {
    .container.not_found {
        // width: 100vw;
        // height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: "Poppins", sans-serif;
        position: relative;
        left: 6vmin;
        text-align: center;
        margin-top: 235px;
    }
    .cog-wheel1,
    .cog-wheel2 {
        transform: scale(0.7);
    }
    .cog1,
    .cog2 {
        width: 40vmin;
        height: 40vmin;
        border-radius: 50%;
        border: 6vmin solid $red;
        position: relative;
    }
    .cog2 {
        border: 6vmin solid $cyan;
    }
    .top,
    .down,
    .left,
    .right,
    .left-top,
    .left-down,
    .right-top,
    .right-down {
        width: 10vmin;
        height: 10vmin;
        background-color: $red;
        position: absolute;
    }
    .cog2 .top,
    .cog2 .down,
    .cog2 .left,
    .cog2 .right,
    .cog2 .left-top,
    .cog2 .left-down,
    .cog2 .right-top,
    .cog2 .right-down {
        background-color: $cyan;
    }
    .top {
        top: -14vmin;
        left: 9vmin;
    }
    .down {
        bottom: -14vmin;
        left: 9vmin;
    }
    .left {
        left: -14vmin;
        top: 9vmin;
    }
    .right {
        right: -14vmin;
        top: 9vmin;
    }
    .left-top {
        transform: rotateZ(-45deg);
        left: -8vmin;
        top: -8vmin;
    }
    .left-down {
        transform: rotateZ(45deg);
        left: -8vmin;
        top: 25vmin;
    }
    .right-top {
        transform: rotateZ(45deg);
        right: -8vmin;
        top: -8vmin;
    }
    .right-down {
        transform: rotateZ(-45deg);
        right: -8vmin;
        top: 25vmin;
    }
    .cog2 {
        position: relative;
        left: -10.2vmin;
        bottom: 10vmin;
    }
    h1 {
        color: #142833;
    }
    .first-four {
        position: relative;
        left: 6vmin;
        font-size: 40vmin !important;
    }
    .second-four {
        position: relative;
        right: 18vmin;
        z-index: -1;
        font-size: 40vmin !important;
    }
    .wrong-para {
        position: absolute;
        bottom: -100px;
        padding: 3vmin 12vmin 3vmin 3vmin;
        font-weight: 600;
        color: #092532;
    }
}