  top: 0;
  width: 100%;
  z-index: 100;
  transition: transform 0.4s;
}
#wrapper-navbar .navbar-brand img {
  width: 151px;
}
#wrapper-navbar .fast-contact {
  position: relative;
  width: 130px;
  display: flex;
  justify-content: center;
  z-index: 10000;
  top: 0;
  right: 0;
}
@media (min-width: 576px) {
  #wrapper-navbar .fast-contact {
    width: 118px;
  }
}
@media (min-width: 768px) {
  #wrapper-navbar .fast-contact {
    position: absolute;
    right: 15px;
    z-index: 1000;
    top: unset;
  }
}
#wrapper-navbar .fast-contact a {
  text-decoration: none;
}
#wrapper-navbar .fast-contact a.phone svg {
  width: 30px;
  height: 30px;
  fill: #04273e;
  transition: all 0.2s;
}
#wrapper-navbar .fast-contact a.mail {
  position: relative;
  left: 20px;
  top: 2px;
}
#wrapper-navbar .fast-contact a.mail svg {
  width: 30px;
  height: 22px;
  fill: #04273e;
  transition: all 0.2s;
}
#wrapper-navbar .fast-contact a:hover.phone svg {
  fill: #ff2e48;
}
#wrapper-navbar .fast-contact a:hover.mail svg {
  fill: #ff2e48;
}
#wrapper-navbar .fast-contact.move {
  top: 330px;
  right: -55px;
  transition: 0.25s ease-in-out;
}
