  background: #04273e;
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  transition: 0.3s ease-in-out;
}

.menu-btn-3 span {
  display: block;
  position: relative;
  margin-top: 2px;
}

.menu-btn-3 span::before {
  top: -8px;
}

.menu-btn-3 span::after {
  top: 8px;
}

.menu-btn-3.active span {
  background: transparent;
}

.menu-btn-3.active span::before {
  top: 0;
  transform: rotate(45deg);
}

.menu-btn-3.active span::after {
  top: 0;
  transform: rotate(-45deg);
